a.doc_action {
    position: relative
}
ul.doc_action_dropdown {
    margin-top: -2px;
    display: none;
    width: 135px;
    padding: 10px;
    text-align: left;
    position: absolute;
    left: -50px;
    background-color: #fff;
    z-index: 1;
    -webkit-box-shadow: 0 0 35px -13px #302c30;
    -moz-box-shadow: 0 0 35px -13px #302c30;
    box-shadow: 0 0 35px -13px #302c30;
}
ul.doc_action_dropdown li {
    font-size: 15px;
    padding-left: 10px;
    padding-bottom: 7px;
}
ul.doc_action_dropdown li a{
    color: #98a0b4;
}
a.doc_action {
    color: #98a0b4;
}
a.doc_action:hover, ul.doc_action_dropdown li a:hover{
    color: #3273dc;
}
a.doc_action:hover ul.doc_action_dropdown {
    display: block;
}
ul.doc_action_dropdown li a span.fa {
    padding-right : 10px;
}
.share_files_control {
    margin-bottom: 15px;
}
.no-show {
    display: none;
}
#erp-doc-share-template {
    width: 100%;
}


